import * as React from "react";
import { graphql, Link, HeadFC, PageProps } from "gatsby";
import { Layout } from "../components/Layout";
import { CategoryBadge } from "../components/CategoryBadge";
import { formatDate, calculateReadTime } from "../utils/date";

interface BlogPostTemplateProps extends PageProps {
    data: {
        markdownRemark: {
            html: string;
            excerpt: string;
            frontmatter: {
                title: string;
                date: string;
                description: string;
                category: string;
                author: string;
            };
        };
        previous: {
            fields: {
                slug: string;
            };
            frontmatter: {
                title: string;
            };
        } | null;
        next: {
            fields: {
                slug: string;
            };
            frontmatter: {
                title: string;
            };
        } | null;
    };
}

const BlogPostTemplate: React.FC<BlogPostTemplateProps> = ({ data }) => {
    const post = data.markdownRemark;
    const { previous, next } = data;
    const readTime = calculateReadTime(post.html);

    return (
        <Layout>
            <article className="py-16">
                <div className="container max-w-4xl">
                    {/* Article Header */}
                    <header className="mb-12">
                        <div className="flex items-center gap-3 mb-6">
                            <CategoryBadge category={post.frontmatter.category} />
                            <span className="text-sm text-[var(--color-text-muted)]">
                                {formatDate(post.frontmatter.date)}
                            </span>
                            <span className="text-sm text-[var(--color-text-muted)]">·</span>
                            <span className="text-sm text-[var(--color-text-muted)]">
                                {readTime} min read
                            </span>
                        </div>

                        <h1 className="text-5xl font-bold font-serif mb-6 text-[var(--color-text)]">
                            {post.frontmatter.title}
                        </h1>

                        {post.frontmatter.description && (
                            <p className="text-xl text-[var(--color-text-secondary)] leading-relaxed">
                                {post.frontmatter.description}
                            </p>
                        )}

                        <div className="mt-6 pt-6 border-t border-[var(--color-border)]">
                            <p className="text-sm text-[var(--color-text-muted)]">
                                By <span className="font-semibold text-[var(--color-text)]">{post.frontmatter.author}</span>
                            </p>
                        </div>
                    </header>

                    {/* Article Content */}
                    <div
                        className="markdown-content"
                        dangerouslySetInnerHTML={{ __html: post.html }}
                    />

                    {/* Article Footer - Navigation */}
                    <footer className="mt-16 pt-8 border-t border-[var(--color-border)]">
                        <nav className="grid grid-cols-1 md:grid-cols-2 gap-8">
                            {previous && (
                                <Link
                                    to={previous.fields.slug}
                                    className="group p-6 border border-[var(--color-border)] hover:border-[var(--color-accent)] transition-colors"
                                >
                                    <p className="text-sm text-[var(--color-text-muted)] mb-2">← Previous</p>
                                    <p className="font-semibold text-[var(--color-text)] group-hover:text-[var(--color-accent)] transition-colors">
                                        {previous.frontmatter.title}
                                    </p>
                                </Link>
                            )}

                            {next && (
                                <Link
                                    to={next.fields.slug}
                                    className="group p-6 border border-[var(--color-border)] hover:border-[var(--color-accent)] transition-colors md:text-right"
                                >
                                    <p className="text-sm text-[var(--color-text-muted)] mb-2">Next →</p>
                                    <p className="font-semibold text-[var(--color-text)] group-hover:text-[var(--color-accent)] transition-colors">
                                        {next.frontmatter.title}
                                    </p>
                                </Link>
                            )}
                        </nav>

                        <div className="mt-8 text-center">
                            <Link to="/blog" className="btn btn-outline">
                                ← Back to All Posts
                            </Link>
                        </div>
                    </footer>
                </div>
            </article>
        </Layout>
    );
};

export default BlogPostTemplate;

export const Head: HeadFC<BlogPostTemplateProps["data"]> = ({ data }) => {
    const post = data.markdownRemark;

    return (
        <>
            <title>{post.frontmatter.title} | ea.caret</title>
            <meta name="description" content={post.frontmatter.description || post.excerpt} />
            <meta property="og:title" content={post.frontmatter.title} />
            <meta property="og:description" content={post.frontmatter.description || post.excerpt} />
            <meta property="og:type" content="article" />
            <meta name="twitter:card" content="summary_large_image" />
            <meta name="twitter:title" content={post.frontmatter.title} />
            <meta name="twitter:description" content={post.frontmatter.description || post.excerpt} />
        </>
    );
};

export const query = graphql`
  query BlogPostBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    markdownRemark(id: { eq: $id }) {
      html
      excerpt(pruneLength: 160)
      frontmatter {
        title
        date
        description
        category
        author
      }
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`;
